
.en-paragraph {
    margin-left: 1.5em;
    margin-bottom: 1em;
}

.en-paragraph-normal {
    margin: 0 0 7.9pt .5in;
    line-height: normal;
}

.en-paragraph-indent {
    margin-bottom:7.9pt;
    text-indent: .5in;
    line-height:normal
}

.en-comment-start {
    margin-right: 0.3em;
    font-size: 12.0pt;
    line-height: 107%;
    font-family: 'Times New Roman', serif;
    color: red;
}

.en-comment-text {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: 'Times New Roman', serif;
    mso-fareast-font-family: 'Times New Roman';
    color: black
}
.en-dash {
    font-size:12.0pt;
    line-height:107%;
    font-family:"Times New Roman",serif
}
.en-citation {
    color: #FFC000;
}

.en-orange {
    font-size:12.0pt;
    line-height:107%;
    font-family:"Times New Roman",serif;
    color: #050505;
}
.en-red {
    font-size:12.0pt;
    line-height:107%;
    font-family:"Times New Roman",serif;
    color:red
}
.en-consequently {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: "Times New Roman", serif;
    color: black;
}

.en-divine {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: "Times New Roman", serif;
}

.en-person-name {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: 'Times New Roman',serif;
    color: #ffc000;
}
.en-text-color {
    color: black;
}

.en-text-font {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: "Times New Roman", serif;
}

.en-foot-note {
    cursor: pointer;
    color: -webkit-link;
}

.en-footnote-reference {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: "Times New Roman", serif;
    mso-fareast-font-family: Calibri;
    mso-ansi-language: EN-US;
    mso-fareast-language: EN-US;
    mso-bidi-language: HE;
}

.en-footnote-char {
    font-size: 12.0pt;
    line-height: 107%;
    font-family: "Times New Roman", serif;
    mso-fareast-font-family: Calibri;
    mso-ansi-language: EN-US;
    mso-fareast-language: EN-US;
    mso-bidi-language: HE;
}

.en-biblical-ref {
    color: -webkit-link;
    cursor: pointer;
}
