@font-face {
    font-family: "SBL Hebrew";
    src: url("../src/fonts/SBL_BLit.ttf");
}

body, html {
    margin: 0;
    width: 100%;
    height: 100%;
    /*font-size: 21px;*/
    /*line-height: 1em;*/
    font-family: "SBL Hebrew", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    margin: 0;
    width: 100%;
    height: 100%;
}

.hebrew-font {
    font-family: "SBL Hebrew" !important;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

a {
    width: 100%;
    height: 100%;
    cursor: pointer;
    font-family: "Times New Roman", Times, serif;
    color: rgb(161, 161, 161);
    text-decoration: none;
}

a:hover {
    color: rgb(82, 82, 82);
    text-decoration: underline;
}

/* https://rimdev.io/css-only-tooltip-for-all-screen-sizes/ */
[data-tip] {
    cursor: pointer;
    position: relative;
}

[data-tip]::after {
    background-color: black;
    border-radius: 0.3rem;
    color: #fff;
    content: attr(data-tip);
    font-size: 1rem;
    font-weight: inherit;
    line-height: 1.15rem;
    visibility: hidden;
    padding: 0.25rem 0.5rem;
    position: absolute;
}

