[data-tip].en-foot-note::after {
    top: 100%;
    left: 50%;
    text-align: left;
    min-width: 150px;
    width: auto;
    direction: ltr;
    transform: translate(-50%);
}

[data-tip].he-foot-note::after {
    top: 100%;
    left: 50%;
    text-align: right;
    min-width: 150px;
    direction: rtl;
    width: auto;
    transform: translate(-50%);
}

[data-tip]:hover::after,
[data-tip][class*="tooltip-"]:hover::before {
    visibility: visible;
    background-color: black;
    opacity: 1;
}